:root {
  --primary-color: #000000;
  --secondary-color: #ffffff;
  --tertiary-color: #90ee90;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: var(--primary-color);
  font-family: Helvetica, Arial, sans-serif;
  background-color: whitesmoke;
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* SCROLLBAR START */
::-webkit-scrollbar {
  width: .8em;
  height: .8em;
}

::-webkit-scrollbar-track {
  background: var(--primary-color);
}

::-webkit-scrollbar-thumb {
  background: var(--secondary-color);
  border: .2em solid var(--primary-color);
  border-radius: 10px;
}

/* syntax to support Firefox browser*/
@supports (scrollbar-color: white black) {
  * {
    scrollbar-color: white black;
  }
}
/* SCROLLBAR END */

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* HEADER + NAV STYLE START */
header {
  background-color: var(--primary-color);
  padding: 20px 40px 0 40px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  /* filter: drop-shadow(0px -20px 25px var(--secondary-color)); */
}

header h1 {
  color: var(--secondary-color);
  font-size: 70px;
  font-weight: bold;
  /* margin: 30px 0 0 0; */
  padding: 5px 0px;
}

header a {
  text-decoration: none;
  color: var(--secondary-color);
}

nav {
  color: var(--secondary-color);
  /* margin: 30px 0 0 0; */
}

nav ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  align-items: center;
}

nav ul li {
  padding: 10px 0px;
  margin: 0px 20px;
  font-size: 15px;
  font-weight: lighter;
}

nav ul li:hover {
  cursor: grab;
}

.css-3yeb0c-MuiModal-root-MuiDrawer-root .MuiDrawer-paper {
  background-color: #000;
}

.navActive {
  color: var(--primary-color);
  background: var(--secondary-color);
  padding: 2px 8px;
  border-radius: 3px;
}
/* HEADER + NAV STYLE END */

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* HERO STYLE START */
.hero {
  background-image: url(./assets/images/hero-grid.jpg);
  height: 350px;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-attachment: fixed;
  align-items: center;
  margin-top: 80px;
  
}

.hero-name {
  background-image: linear-gradient(#000, #191919, #333333);
  color: var(--secondary-color);
  border: 2px solid var(--secondary-color);
  border-radius: 2px;
  position: relative;
  margin-bottom: 65px;
}

.hero-name p {
  padding: 35px 70px;
  font-size: 70px;
  font-style: italic;
  font-weight: bold;
  text-align: center;
}

.hero-text {
  color: var(--secondary-color);
  position: absolute;
  left: 36%;
  top: 65%;
  white-space: nowrap;
  transform: rotate(-7deg);
}

.hero-text p {
  font-size: 90px;
  font-family: Brush Script MT;
  padding: 0;
}
/* HERO STYLE END */

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* SECTIONS STYLE START */
.sections {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  padding: 50px 30px;
  margin: 15px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.sections h2 {
  border-right: 5px solid var(--secondary-color);
  font-size: 32px;
  padding: 20px;
  margin: 10px;
  flex: 10%;
  display: flex;
  justify-content: center;
  line-height: 1.2;
}

.section-content {
  flex: 80%;
  margin-left: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.section-content img:hover {
  filter: none;
  opacity: 100%;
}
/* SECTIONS STYLE END */

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* ABOUT ME STYLE START */
.about-me {
  display: flex;
  align-items: center;
}

.about-me img {
  width: 25%;
  padding: 0 20px;
  /* margin-top: 0px; */
  filter: grayscale(100%);
}

.about-me p {
  width: 70%;
  float: right;
  margin: 40px 50px;
  text-align:left;
  line-height: 1.8;
  font-size: 17px;
}
/* ABOUT ME STYLE END */

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* PORTFOLIO/PROJECT/MY WORK STYLE START */
.work {
  padding-right: 35px;
}

.work-h2tag {
  align-self: flex-start;
}

.wrapper {
  width: 50%;
}

.container {
  border: 5px solid var(--secondary-color);
  width: 95%;
  padding: 4px;
  position: relative;
  margin: 13px;
}

.container img {
  display: flex;
  width: 100%;
  height: 285px;
  opacity: 100%;
  filter: grayscale(100%);
}

.text-block {
  position: absolute;
  bottom: 25px;
  background: var(--secondary-color);
  color: var(--primary-color);
  padding: 6px 25px;
  border: 3px solid var(--primary-color);
  border-left: 0;
  font-weight: bold;
  font-style: italic;
  font-size: 20px;
  line-height: 1;
}

.text-block a {
  color: var(--primary-color);
  text-decoration: none;
  font-size: 13px;
  font-weight: lighter;
  font-style: italic;
}

.text-block a:hover {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border-radius: 2px;
  padding: 2px 6px;
}
/* PORTFOLIO/PROJECT/MY WORK STYLE END */

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* CONTACT STYLE START */
.error-text {
  color: rgb(255, 0, 0);
  margin: 4px;
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 70px;
}

.contact h2 {
  border-right: none;
  padding: 0;
  margin-bottom: 20px;
  font-size: 36px;
  font-weight: normal;
  letter-spacing: .02em;
}

#contact-form {
  background-color: var(--primary-color);
  border: 2px solid white;
  border-radius: 5px;
  filter: drop-shadow(-3px 3px 4px #CED4DA);
  padding: 25px;
  width: 50%;
}

.contact label {
  font-size: 22px;
}

.contact input,
.contact textarea {
  width: 100%;
  font-size: 18px;
  margin: 5px 0 20px 0;
  padding: 5px;
  border-radius: 3px;
}

.contact button {
  font-size: 20px;
  background-color: var(--secondary-color);
  color: var(--primary-color);
  /* border: 2px solid white; */
  border-radius: 5px;
  width: 17%;
  text-align: center;
  margin-top: 5px;
  padding: 2px;
  flex-grow: 5;
}

.contact button:hover,
.contact button:focus {
  border: 2px solid white;
  box-shadow: inset 6em 0 0 0 var(--primary-color);
  transition: 0.5s;
  color: var(--tertiary-color);
}
/* CONTACT STYLE END */

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* RESUME STYLE START */
.resume {
  align-items: flex-start;
  justify-content: space-evenly;
}

.resume h2 {
  border-right: none;
  border-bottom: 5px solid var(--secondary-color);
  padding-bottom: 5px ;
  margin: 0px 15px 20px 15px;
}

.resume ul {
  margin-left: 0px;
  flex-direction: column;
  list-style: none;
  font-size: 18px;
  width: 300px;
  line-height: 1.5;
  text-align: center;
}
/* RESUME STYLE END */

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -  */

/* FOOTER STYLE START */
footer {
  background-color: var(--primary-color);
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  padding: 20px;
}

footer ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  align-items: center;
}

footer p {
  text-decoration: none;
  color: var(--secondary-color);
  /* padding: 10px 20px; */
  font-size: 16px;
  margin-top: 15px;
}

footer a {
  margin: 0 20px;
}

#octo:hover {
  filter: drop-shadow(3px 1px 0.5px #2FBB4F);
}

#linked:hover {
  /* filter: invert(40%) sepia(98%) saturate(3980%) hue-rotate(180deg) brightness(102%) contrast(103%); */
  filter: drop-shadow(3px 1px 0.5px #20A7FF);
}

#stack:hover {
  /* filter: invert(20%) sepia(32%) saturate(6051%) hue-rotate(-25deg) brightness(106%) contrast(107%); */
  filter: drop-shadow(3px 1px 0.5px #EC7713);
}
/* FOOTER STYLE END */







